/* stylelint-disable selector-class-pattern */

@mixin ag-theme-mill-part-grid {
  .ag-row-selected {
    border-bottom: var(--mill-selected-row-border-bottom);
  }

  .ag-cell {
    font-family: var(--mill-cell-font-family);
    font-size: var(--mill-cell-font-size);
    letter-spacing: var(--mill-cell-letter-spacing);
    border-right-color: transparent;

    &.mill-ag-numeric-cell {
      font-family: "RobotoMono400", monospace;
    }

    &:hover {
      .mill-ag-cell-renderer-primitive__copy {
        visibility: visible;
        right: 0;
        opacity: 1;
      }
    }
  }
}
