@use "ag-grid-community/styles" as ag;

@import "fonts/mill-valdemarius-normal/mill-valdemarius-normal";
@import "typography";
@import "colors";

@import "parts/variables";
@import "parts/density";
@import "parts/root";
@import "parts/checkbox";
@import "parts/header";
@import "parts/grid";

@include ag.grid-styles(
  (
    themes: (
      mill: (),
      /*mill-dark: (),*/
    ),
    background-color: $other-white,
    foreground-color: $grey-60,
    secondary-foreground-color: $main-primary-dark,
    data-color: $main-primary-dark,
    header-foreground-color: $grey-60,
    header-background-color: $other-white,
    subheader-background-color: $other-white,
    odd-row-background-color: $other-white,
    //tooltip-background-color:
    //disabled-foreground-color:
    //subheader-toolbar-background-color:
    //control-panel-background-color:
    //side-button-selected-background-color:
