    modal-overlay-background-color: transparent,
    row-hover-color: $grey-10,
    column-hover-color: $grey-10,
    range-selection-border-color: transparent,
    range-selection-border-style: solid,
    range-selection-background-color: $red-10,
    range-selection-background-color-2: $red-10,
    range-selection-background-color-3: $red-10,
    range-selection-background-color-4: $red-10,
