    borders-critical: solid 1px,
    borders-secondary: solid 1px,
    secondary-border-color: $grey-20,
    row-border-style: solid,
    row-border-width: 1px,
    row-border-color: $grey-20,
    header-cell-moving-background-color: $main-primary-light,
