    //input-border-color:
    borders-input-invalid: none,
    //input-border-color-invalid:
    borders-side-button: none,
    border-radius: 8px,
    //invalid-color:
    //input-disabled-border-color:
    //input-disabled-background-color:
    checkbox-background-color: $other-white,
    checkbox-border-radius: 4px,
    checkbox-checked-color: $red-50,
    checkbox-unchecked-color: $other-white,
    checkbox-indeterminate-color: $red-50,
