      (
        $gilroy-500-medium,
        sans-serif,
      ),
    font-size: 14px,
    icon-font-family: millValdemariusNormal,
    //card-radius:
    //card-shadow:
    popup-shadow:
      (
        0 4px 10px 0 rgb(0 0 0 / 3%),
        0 2px 7px 0 rgb(0 0 0 / 10%),
        0 0 2px 0 rgb(0 0 0 / 20%),
      ),
  )
);

.ag-theme-mill,
.ag-theme-mill-dark {
  @include ag-theme-mill-part-variables;
  @include ag-theme-mill-part-density;
  @include ag-theme-mill-part-root;
  @include ag-theme-mill-part-checkbox;
  @include ag-theme-mill-part-header;
  @include ag-theme-mill-part-grid;
}
