/* stylelint-disable selector-class-pattern */

@mixin ag-theme-mill-part-header {
  .ag-header-cell {
    font-family: var(--mill-header-cell-font-family);
    font-size: var(--mill-header-cell-font-size);
    letter-spacing: var(--mill-header-cell-letter-spacing);

    &.ag-header-cell-sortable {
      .mill-ag-header {
        cursor: pointer;
      }
    }

    &.ag-right-aligned-header {
      .mill-ag-header {
        justify-content: flex-end;
      }
    }

    &.mill-checkbox-header-cell {
      padding-inline: 16px 0;
    }

    &.ag-floating-filter {
      padding: 0;
      border-right: 1px var(--ag-cell-horizontal-border);

      .mill-ag-floating-filter {
        width: 100%;
        height: 100%;
        box-sizing: border-box;

        &.mill-ag-right-aligned-floating-filter {
          .mill-primitive-form-field__input {
            text-align: right;
          }
        }

        .mill-primitive-form-field__content::before {
          border-radius: 0;
          border: none;
        }
      }
    }
  }
}
