@use "colors";

.ag-cell {
  .mill-ag-cell-renderer-primitive {
    &__content {
      height: 100%;
      display: flex;
      flex-direction: row;
      align-items: center;
    }
  }
}
.ag-root-wrapper {
  border-color: colors.$grey-20 !important;
}

.ag-tool-panel-wrapper {
  min-width: 250px;

  .ag-virtual {
    &-list {
      &-item {
        position: initial !important;
        padding-inline: 11px;
      }

      &-container {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-block-start: 10px;
      }
    }
  }

  .ag-column-select-header {
    padding-inline: 11px;
  }

  .ag-virtual-list-container {
    height: max-content !important;
  }
}

.ag-side-buttons {
  display: none;
}

.ag-row-selected {
  border-bottom: solid colors.$grey-20 !important;
}
