@import "typography";

@mixin ag-theme-mill-part-variables {
  /* %s2 */
  --mill-header-cell-font-family: #{$gilroy-700-bold}, sans-serif;
  --mill-header-cell-font-size: 14px;
  --mill-header-cell-line-height: 20px;
  --mill-header-cell-letter-spacing: 0.18px;

  /* %p2 */
  --mill-cell-font-family: #{$gilroy-500-medium}, sans-serif;
  --mill-cell-font-size: 14px;
  --mill-cell-line-height: 20px;
  --mill-cell-letter-spacing: 0.25px;

  /* checkbox */
  --mill-checkbox-border-width: 2px;
  --mill-checkbox-box-size: calc(var(--ag-icon-size, 24px) - var(--mill-checkbox-border-width) * 2);

  /* icons */
  --ag-icon-font-code-ageregation: "";
  --ag-icon-font-code-arrows: "";
  --ag-icon-font-code-asc: "\e006";
  --ag-icon-font-code-cancel: "\e018";
  --ag-icon-font-code-chart: "\e064";
  --ag-icon-font-code-checkbox-checked: "";
  --ag-icon-font-code-checkbox-indeterminate: "";
  --ag-icon-font-code-checkbox-unchecked: "";
  --ag-icon-font-code-color-picker: "";
  --ag-icon-font-code-columns: "";
  --ag-icon-font-code-contracted: "\e008";
  --ag-icon-font-code-copy: "\e03f";
  --ag-icon-font-code-cross: "\e014";
  --ag-icon-font-code-csv: "\e067"; // TODO: Add icon?
  --ag-icon-font-code-cut: "";
  --ag-icon-font-code-desc: "\e005";
  --ag-icon-font-code-excel: "\e067"; // TODO: Add icon?
  --ag-icon-font-code-expanded: "\e007";
  --ag-icon-font-code-eye-slash: "\e03b";
  --ag-icon-font-code-eye: "\e033";
  --ag-icon-font-code-filter: "";
  --ag-icon-font-code-first: "\e00a";
  --ag-icon-font-code-grip: "";
  --ag-icon-font-code-group: "";
  --ag-icon-font-code-last: "\e009";
  --ag-icon-font-code-left: "\e003";
  --ag-icon-font-code-linked: "";
  --ag-icon-font-code-loading: "\e01c";
  --ag-icon-font-code-maximize: "\e073";
  --ag-icon-font-code-menu: "\e012";
  --ag-icon-font-code-minimize: "\e074";
  --ag-icon-font-code-next: "\e008";
  --ag-icon-font-code-none: "";
  --ag-icon-font-code-not-allowed: "";
  --ag-icon-font-code-paste: "";
  --ag-icon-font-code-pin: "";
  --ag-icon-font-code-pivot: "";
  --ag-icon-font-code-previous: "\e007";
  --ag-icon-font-code-radio-button-off: "";
  --ag-icon-font-code-radio-button-on: "";
  --ag-icon-font-code-right: "\e004";
  --ag-icon-font-code-save: "\e01f";
  --ag-icon-font-code-small-down: "";
  --ag-icon-font-code-small-left: "";
  --ag-icon-font-code-small-right: "";
  --ag-icon-font-code-small-up: "";
  --ag-icon-font-code-tick: "\e013";
  --ag-icon-font-code-tree-closed: "\e008";
  --ag-icon-font-code-tree-indeterminate: "";
  --ag-icon-font-code-tree-open: "\e00b";
  --ag-icon-font-code-unlinked: "";
}
