.mill-badge {
  position: relative;

  // Styles for standard badge
  &::after {
    @extend %p3; // Extends style and overwrite font-family below

    font-family: $gilroy-700-bold, sans-serif;
    text-align: center;
    content: attr(data-badge-value);
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    box-sizing: border-box;
    border-radius: 16px;
    height: 16px;
    background-color: $red-50;
    color: $other-white;
    transition: background-color 150ms ease-in-out;
  }
}

// Special styles for button's badge
button.mill-badge {
  &_size_thin {
    &::after {
      width: 6px;
      height: 6px;
      top: 4px;
      right: 4px;
    }

    & > mill-icon svg {
      /* stylelint-disable declaration-colon-newline-after */
      clip-path: path(
        "M16.9996 0C16.3719 0.835661 16 1.87439 16 3C16 5.76142 18.2386 8 21 8C22.1256 8 23.1643 7.62806 24 7.00037V24H0V0H16.9996Z"
      );
      /* stylelint-enable declaration-colon-newline-after */
    }
  }

  &_size_small {
    &::after {
      width: 16px;
    }

    & > mill-icon svg {
      /* stylelint-disable declaration-colon-newline-after */
      clip-path: path(
        "M10.8321 0C10.2969 1.22492 10 2.57779 10 4C10 9.52285 14.4772 14 20 14C21.4222 14 22.7751 13.7031 24 13.1679V24H0V0H10.8321Z"
      );
      /* stylelint-enable declaration-colon-newline-after */
    }
  }

  &_size_medium {
    &::after {
      width: 22px;
    }

    & > mill-icon svg {
      /* stylelint-disable declaration-colon-newline-after */
      clip-path: path(
        "M4.83209 0C4.2969 1.22492 4 2.57779 4 4C4 9.52285 8.47715 14 14 14H20C21.4222 14 22.7751 13.7031 24 13.1679V24H0V0H4.83209Z"
      );
      /* stylelint-enable declaration-colon-newline-after */
    }
  }

  &_size_large {
    &::after {
      width: 29px;
    }

    & > mill-icon svg {
      /* stylelint-disable declaration-colon-newline-after */
      clip-path: path(
        "M0 11.1414V24H24V13.1679C22.7751 13.7031 21.4222 14 20 14H7C4.27455 14 1.80375 12.9097 0 11.1414Z"
      );
      /* stylelint-enable declaration-colon-newline-after */
    }
  }
}
