.mill-scrollable {
  position: relative;
  scrollbar-width: none;

  &__thumb {
    opacity: 0;
    display: block;
    background-color: rgba(0, 0, 13, 0.22);
    border-radius: 50px;
    width: 4px;
    position: fixed;
    top: 6px;
    right: 6px;
    cursor: pointer;
    transition-property: background-color, opacity;
    transition-duration: 150ms;
    transition-timing-function: ease-in-out;

    &:hover {
      background-color: rgba(0, 0, 13, 0.5);
    }
  }

  &_hovered {
    .mill-scrollable__thumb {
      opacity: 1;
    }
  }

  &::-webkit-scrollbar {
    display: none;
  }

