/* stylelint-disable selector-class-pattern */

@import "colors";

@mixin ag-theme-mill-density-x-low {
  &.mill-density-x-low {
    --ag-row-height: 88px;

    .ag-header-cell,
    .ag-cell {
      /* Add vertical borders, same as horizontal */
      border-right: 1px var(--ag-cell-horizontal-border);
    }
  }
}

@mixin ag-theme-mill-density-low {
  &.mill-density-low {
    --ag-row-height: 56px;

    .ag-header-cell,
    .ag-cell {
      /* Add vertical borders, same as horizontal */
      border-right: 1px var(--ag-cell-horizontal-border);
    }
  }
}

@mixin ag-theme-mill-density-medium {
  &.mill-density-medium {
    --ag-row-height: 48px;
  }
}

@mixin ag-theme-mill-density-high {
  &.mill-density-high {
    --ag-row-height: 40px;
  }
}

@mixin ag-theme-mill-part-density {
  @include ag-theme-mill-density-x-low;
  @include ag-theme-mill-density-low;
  @include ag-theme-mill-density-medium;
  @include ag-theme-mill-density-high;
}
