/* stylelint-disable selector-class-pattern */

@mixin ag-theme-mill-part-root {
  .ag-root-wrapper {
    border-color: var(--mill-table-border-color);
    border-radius: var(--ag-border-radius);

    &.ag-keyboard-focus {
      .ag-header-cell:focus::after,
      .ag-header-group-cell:focus::after {
        display: none;
      }
    }
  }
}
