/* stylelint-disable selector-class-pattern */

@import "colors";

@mixin ag-theme-mill-part-checkbox {
  .ag-checkbox-input-wrapper {
    background-color: transparent;

    &::after {
      content: "";
      width: var(--mill-checkbox-box-size);
      height: var(--mill-checkbox-box-size);
      margin: var(--mill-checkbox-border-width);
      border: var(--mill-checkbox-border-width) solid $grey-50;
      border-radius: var(--ag-checkbox-border-radius);
      background-color: var(--ag-checkbox-background-color);
      transition: border-color 150ms ease-in-out, background-color 100ms ease-in-out;
    }

    &.ag-checked::after,
    &.ag-indeterminate::after {
      background-repeat: no-repeat;
      background-position: center;
    }

    &.ag-checked::after {
      border-color: var(--ag-checkbox-checked-color);
      background-color: var(--ag-checkbox-checked-color);
      background-image: url("data:image/svg+xml,%3Csvg id='ok' width='12' height='12' viewBox='0 0 12 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M3.52539 9.09619L4.9396 10.5104L6.35382 9.09619L11.3036 4.14644L9.88935 2.73223L4.9396 7.68198L2.34342 5.08578L0.929201 6.5L3.52539 9.09619Z' fill='white' /%3E%3C/svg%3E%0A");
    }

    &.ag-indeterminate::after {
      border-color: var(--ag-checkbox-indeterminate-color);
      background-color: var(--ag-checkbox-indeterminate-color);
      background-image: url("data:image/svg+xml,%3Csvg id='minus' width='12' height='12' viewBox='0 0 12 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 5L11 5V7H1V5Z' fill='white' /%3E%3C/svg%3E%0A");
    }
  }
}
